/**************** base styles  ******************/

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "chicle", cursive;
  flex-direction: column;
  scroll-behavior: smooth;
}

body {
  background-image: linear-gradient(to right, grey, black);
}

button {
  color: black;
  width: fit-content;
}

input {
  color: black;
  width: fit-content;
}

p {
  padding: 1%;
}

h1 {
  color: white;
}

button {
  cursor: pointer;
}

/**************** animations  ******************/

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes slide-up-fade-in {
  0% {
    opacity: 0;
    transform: translateY(10%);
  }
  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: translateY(-10%);
  }
}

/**************** icon styles  ******************/

.owf {
  color: white;
}

.owf-10x {
  font-size: 10em;
}

/* sun style */

.owf-800:before,
.owf-800-d:before {
  color: orange;
  margin-right: 15px;
}

.delete {
  cursor: pointer;
  opacity: 0.5;
}

.delete:hover {
  opacity: 1;
}

.delErr {
  cursor: pointer;
}

/* .star:hover {
  color: rgb(207, 207, 83);
} */

.favourited {
  color: rgb(207, 207, 83);
}

/* .favourited:hover {
  color: black;
} */

.star {
  cursor: pointer;
  margin-right: 4px;
  font-size: 1.05em;
}

.spinner {
  animation: rotate 2s infinite linear;
  color: white;
  fill: #f38e82;
}

.sun {
  border-right: none;
}

/**************** search and input section styles  ******************/

.user-input {
  display: flex;
  margin: auto;
  text-align: center;
  width: fit-content;
}

#search {
  margin: auto;
  margin-top: 2%;
  border-radius: 0px;
  padding: 2%;
}

#searchInp {
  margin: auto;
}

.overlay {
  opacity: 0.05;
}

/**************** weather data section styles  ******************/

.data {
  margin: auto;
  text-align: center;
  margin-top: 20%;
  width: fit-content;
}

.weatherData {
  border: 8px solid #fff;
  border-radius: 10px;
  width: 250px;
  background-color: black;
  margin-bottom: 20%;
  transition: 500ms;
}

.weatherData:hover {
  transform: translateY(-2%);
}

.weatherHeader {
  border: none;
  background-color: lightblue;
  padding: 2%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.main-details {
  color: #fff;
}

.country {
  border-right: none;
}

.more-detail {
  color: black;
  padding: 5%;
  margin-top: 15%;
  background-color: rgb(255, 255, 255);
}

/**************** favourite popup message styles  ******************/

#render {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 115px;
  padding: 5%;
  border-radius: 0px;
}

.favourites-section {
  display: flex;
  margin: auto;
  margin-top: 5%;
}

.favourite-message {
  animation: slide-up-fade-in ease 1s forwards;
  border-radius: 25px;
  color: black;
  background-color: lightblue;
  display: flex;
  margin: auto;
  margin-top: 10%;
  padding: 5%;
  transition: 100ms;
  width: fit-content;
  position: absolute;
  top: 5%;
  left: 20%;
  z-index: 99;
}

.fav-msg {
  padding: 4%;
}

.bookmark {
  margin-right: 5%;
}

/**************** error popup message styles  ******************/

.error-container {
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  color: white;
  display: flex;
  font-size: 1.2em;
  margin: auto;
  margin-top: 10%;
  padding: 5%;
  text-align: center;
  transition: 200ms;
  width: fit-content;
  z-index: 99;
}

.error-container-one {
  border-radius: 25px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  color: white;
  display: flex;
  font-size: 1.2em;
  margin: auto;
  margin-top: 10%;
  padding: 5%;
  text-align: center;
  transition: 200ms;
  width: fit-content;
  z-index: 99;
}

.error-container:hover {
  transform: scale(1.1);
}

.errMessage {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.hide {
  opacity: 0;
  transform: scale(0.01);
}

/************************** iPad styles  **********************************************************************************************************/

@media only screen and (min-width: 768px) {
  /**************** search and input section styles (ipad)  ******************/

  .user-input {
    display: flex;
    margin: auto;
    text-align: center;
    width: fit-content;
  }

  #search {
    padding: 1%;
  }

  #searchInp {
    padding: 1%;
  }

  /**************** weather data section styles (ipad)  ******************/

  .weatherData {
    margin-left: 10%;
    margin-right: 5%;
  }

  .data {
    display: flex;
    flex-flow: wrap;
    flex-direction: row;
    justify-content: center;
    margin-top: 10%;
  }

  .weatherData {
    width: 225px;
    margin-bottom: 10%;
    font-size: 0.8em;
  }

  .weatherHeader {
    padding: 1%;
  }

  .more-detail {
    padding: 3%;
    margin-top: 5%;
  }

  /**************** favourite popup message styles (ipad)  ******************/

  #render {
    padding: 3%;
  }

  /**************** error popup message styles (ipad)  ******************/

  .error-container {
    font-size: 1.1em;
    margin-top: 5%;
    padding: 2%;
  }

  .error-container-one {
    font-size: 1.1em;
    margin-top: 5%;
    padding: 2%;
  }

  .errMessage {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
}

/************************** Desktop styles  **********************************************************************************************************/

@media only screen and (min-width: 1068px) {
  /**************** icon styles (desktop)  ******************/

  .star:hover {
    color: rgb(207, 207, 83);
  }

  .favourited:hover {
    color: black;
  }

  /**************** search and input section styles (ipad)  ******************/

  .user-input {
    font-size: 1.1em;
  }

  #search {
    padding: 2%;
    margin-top: 5%;
  }

  /**************** weather data section styles (ipad)  ******************/

  .weatherData {
    margin-left: 10%;
    margin-right: 5%;
  }

  .data {
    margin-top: 3%;
    width: max-content;
  }

  .weatherData {
    width: 330px;
    margin-bottom: 8%;
    font-size: 1.1em;
  }

  .weatherHeader {
    padding: 1%;
  }

  /**************** error popup message styles (desktop)  ******************/

  .error-container {
    font-size: 1.3em;
    margin-top: 5%;
    padding: 2%;
  }

  .error-container-one {
    font-size: 1.1em;
    margin-top: 5%;
    padding: 2%;
  }
}
